import axios from "axios";
import { GETTRACKJSON } from "./constant";

var changeList = function changeList(data) {
  return {
    type: GETTRACKJSON,
    data: data
  };
};

export var getSoundJson = function getSoundJson() {
  return function (dispatch) {
    return axios.get("http://localhost:4002/api/track.json").then(function (res) {
      var data = res.data.data;
      dispatch(changeList(data));
    }).catch(function (e) {
      console.error(e, "请求发生错误");
    });
  };
};