import axios from "axios";
import { CHANGE_LIST } from "./constant";

var changeList = function changeList(list) {
  return {
    type: CHANGE_LIST,
    list: list
  };
}; // 异步操作的action 采用thunk中间件处理异步


export var getHomeList = function getHomeList() {
  return function (dispatch) {
    return axios.get("http://localhost:4002/api/album.json").then(function (res) {
      var list = res.data.data;
      dispatch(changeList(list));
    }).catch(function (e) {
      console.error(e, "请求发生错误");
    });
  };
};